.cardv2 {
	display: block;
	border-radius: 4px;
	overflow: hidden;

	// depth--emboss
	border-radius: 8px;
	border: solid 1px #d0d0d0;
  
	text-align: left;
	text-decoration: none !important;
	background-position: top left;
	background-repeat: no-repeat;
	overflow: hidden;
	margin-bottom: 30px;
	background-color: $color-snow-white;
	transition: all 0.2s ease !important;
	color: $color-carbon;
	
	&:hover {
		border: 1px solid $color-auxiliary-light;
		transition: all 0.2s ease !important;
		color: $color-carbon;
	}
	
	//Area Card starts here
	&--area {
		padding: 24px;
		min-width: 272px;
		width: 272px;
		display: inline-block !important;
		margin-right: 13px;
		
		h3 {
			font-size: $font-size-large;
			font-weight: $font-weight-bold-extra;
			color: $color-black;
			line-height: normal;
			margin-bottom: 0px;
			display: block;
			height: 56px;
		}
		
		&__desc {
			color: $color-cement-dark;
			font-size: $font-size-medium;
			line-height: 1.43;
			padding-bottom: 16px;
		}
		
		&__link {
			font-weight: $font-weight-semi_bold;
			font-size: $font-size-large;
			color: $color-har-blue;
			margin-bottom: 24px;
			display: block;
		}
		
		&__map {
			background-repeat: no-repeat;
			background-position: center center;
			background-size: cover;
			border-radius: 4px;
			overflow: hidden;
			height: 160px;
		}
	}
	//Area Card starts here
	
	
	//Neighbor Card starts here	
	&--neighbors{
		display: flex !important;
		flex-flow: wrap;
		font-size: $font-size-medium;
		padding: 16px;
		color: $color-carbon;
		margin-bottom: 8px;
		
		@media screen and (max-width: 676px) {
			padding:12px;
		}
		
		&__address {
			flex-grow: 1;
			align-self: center;			
			font-weight: $font-weight-semi_bold;
			color: $color-har-blue;
			padding-right: 10px;
			
			@media screen and (max-width: 676px) {
				flex-basis: 100%;
				padding-bottom: 10px;
			}
		}
		
		&__subdiv {
			width: 140px;
			align-self: center;
			padding-right: 10px;
			
			@media screen and (max-width: 676px) {
				flex-basis: 100%;
				padding-bottom: 10px;
			}
		}
		
		&__zip {
			width: 70px;
			align-self: center;
			padding-right: 10px;
		}
		
		&__city {
			width: 70px;
			align-self: center;
			padding-right: 10px;
		}
		
		&__state {
			width: 100px;
			align-self: center;
			text-align: right;
		}		
	}
	
	//rating card
	&--rating{
		display: block;
		padding: 16px;
		color: $color-carbon;
		position: relative;
		
		@media screen and (max-width: 676px) {
			padding:12px;
		}
		
		&__icons {
			position: absolute;
			right: 16px;
			top: 16px;
			
			@media screen and (max-width: 600px) {
				right: 8px;
				top: 8px;
			}
			
			> div {
				display: inline-block;
				height: 26px;
				width: 26px;
				opacity: 0.3;
				text-align: center;
				
				&:hover {
					opacity: 1;
				}				
			}
		}
		&__prop {
			display: flex;
			margin-bottom: 16px;
			
			@media screen and (max-width: 600px) {
				margin-top: 24px;
			}
			
			&_img {
				width: 88px;
				min-width: 88px;
				height: 72px;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: cover;
				border-radius: 4px;
			}
			&_text {
				padding-left: 15px;
				align-self: center;
				flex-grow: 1;
				
				h4 {
					font-size: $font-size-large_extra;
					font-weight: $font-weight-semi_bold;
					margin-bottom: 0px;
					
				}
				&_desc {
					color: $color-cement-dark;
					font-size: $font-size-medium;
					font-weight: $font-weight-bold;
					
					span {
						font-weight: normal;
					}
					
				}
			}
			
		}
		
		&__chart {
			display: flex;
			padding-bottom: 16px;

			@media screen and (max-width: 600px) {
				flex-direction: column;
			}

			&__outof {
				width: 160px;
								
				@media screen and (max-width: 600px) {
					width: 100%;
					margin-bottom: 16px;
				}
				
				&--inner {
					height: 96px;
					min-width: 160px;
					background-color: $color-cloudy-sky-light;				
					color: $color-auxiliary;
					font-size: $font-size-large;
					font-weight: $font-weight-semi_bold;
					text-align: center;
					padding: 12px;
					border-radius: 4px;	
	
					span {
						font-size: $font-size-large_extra_extra_extra_extra;
						display: block;
						text-align: center;
						line-height: 1;
					}
				}
				
				&--stars {
					text-align: center;
					padding-top: 8px;
					
					img {
						width: 24px;
					}
				}
			}
			
			&__ratings{
				padding-left: 16px;
				flex-grow: 1;
				align-self: start;
				
				@media screen and (max-width: 600px) {
					width: 100%;
					padding-left: 0px;
				}
			}
		}	
		
		p {
			font-size: $font-size-medium;
			color: $color-carbon;
			margin-bottom: 16px;
			display: block;
			text-align: left;
		}		
	}
	//rating card
	
	//Badges card
	&--badges {
		padding: 24px;		
		display: block;
		 
		&__desc {
			color: $color-cement-dark;
			font-size: $font-size-medium;
			line-height: 1.43;
			padding-bottom: 14px;
			
			@media screen and (max-width: 767px) {
				font-size: $font-size-small;
				padding-bottom: 0px;
				height: 64px;
			}
		}
		
		&__link {
			font-weight: $font-weight-semi_bold;
			font-size: $font-size-medium;
			color: $color-har-blue;
			display: block;
			
			@media screen and (max-width: 767px) {
				font-size: $font-size-small;
			}
		}
		
		@media screen and (max-width: 767px) {
			padding: 16px;
		}
		
		.label--badges {
			@media screen and (max-width: 767px) {
				font-size: $font-size-medium;
				height: 26px;
			}
		}
	}
	//Badges card
	
	
	//appraisers Card starts here	
	&--appraisers{
		display: flex !important;
		align-items: end;
		font-size: $font-size-medium;
		line-height: 1.7;
		padding: 20px;
		margin-bottom: 8px;
		
		@media screen and (max-width: 776px) {
			padding:12px;
			display: block !important;
		}
		
		&__links {
			h3 {
				font-weight: $font-weight-semi_bold;
				color: $color-carbon;
			}
			flex-grow: 1;
			padding-right: 10px;
			a {
				display: block;
			}
			@media screen and (max-width: 776px) {
				flex-basis: 100%;
				padding-bottom: 15px;
			}
		}
		
		&__viewall {
			flex-grow: 1;
			font-weight: $font-weight-semi_bold;
			text-align: right;
			
			@media screen and (max-width: 776px) {
				text-align: left;
			}
		}		
			
	}

}